/* General Styles */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #1a1a1a; /* Dark background */
  color: #ffffff; /* White text */
}

.app {
  display: flex;
  height: 100vh;
}

/* Sidebar Styles */
.sidebar {
  width: 25%;
  background-color: #262626; /* Dark gray */
  border-right: 1px solid #ffffff; /* White border */
  padding: 20px;
  box-sizing: border-box;
  overflow-y: auto;
  transition: width 0.3s ease;
  display: flex;
  flex-direction: column;
}

.sidebar.closed {
  width: 80px; /* Collapsed width */
  padding: 20px 10px; /* Adjust padding for collapsed state */
}

.sidebar.closed .sidebar-header {
  justify-content: center; /* Center logo when collapsed */
}

.sidebar.closed .app-title,
.sidebar.closed .chat-history-title,
.sidebar.closed .chat-history,
.sidebar.closed .profile-section {
  display: none; /* Hide other elements when collapsed */
}

.sidebar-header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.logo {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
  border: 2px solid #ffffff; /* White border */
}

.app-title {
  font-size: 32px;
  font-weight: bold;
  color: #ffffff; /* White text */
}

.chat-history-title {
  font-size: 18px;
  font-weight: 600;
  color: #ffffff; /* White text */
  margin-bottom: 10px;
}

.chat-history {
  display: flex;
  flex-direction: column;
  gap: 10px;
  flex: 1; /* Allow chat history to take up remaining space */
  overflow-y: auto; /* Enable scrolling for chat history */
}

.chat-history-item {
  background-color: #333333; /* Dark gray */
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #ffffff; /* White border */
}

.user-message,
.ai-message {
  font-size: 16px;
  color: #ffffff; /* White text */
  margin: 0;
}

.message-label {
  font-weight: 600;
}

/* Profile Section Styles */
.profile-section {
  margin-top: auto; /* Push to the bottom */
  padding: 20px;
  background-color: #333333; /* Dark gray */
  border-top: 1px solid #ffffff; /* White border */
  display: flex;
  align-items: center;
  gap: 10px;
}

.profile-image {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 2px solid #ffffff; /* White border */
}

.profile-details {
  display: flex;
  flex-direction: column;
}

.profile-name {
  font-size: 16px;
  font-weight: 600;
  color: #ffffff; /* White text */
  margin: 0;
}

.profile-email {
  font-size: 14px;
  color: #cccccc; /* Light gray text */
  margin: 0;
}

/* Main Chat Area Styles */
.main-chat {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.chat-header {
  background-color: #262626; /* Dark gray */
  padding: 20px;
  border-bottom: 1px solid #ffffff; /* White border */
  display: flex;
  align-items: center;
}

.sidebar-toggle {
  background: none;
  border: none;
  color: #ffffff; /* White text */
  font-size: 20px;
  cursor: pointer;
  margin-right: 10px;
}

.profile {
  display: flex;
  align-items: center;
}

.profile-photo {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
  border: 2px solid #ffffff; /* White border */
}

.profile-name {
  font-size: 18px;
  font-weight: 600;
  color: #ffffff; /* White text */
  margin: 0;
}

.profile-status {
  font-size: 14px;
  color: #ffffff; /* White text */
  margin: 0;
}

.chat-messages {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
  background-color: #1a1a1a; /* Dark background */
}

.message {
  max-width: 70%;
  padding: 10px 15px;
  border-radius: 8px;
  margin-bottom: 10px;
  position: relative;
}

.message.user {
  background-color: #333333; /* Dark gray */
  color: #ffffff; /* White text */
  margin-left: auto;
  border: 1px solid #ffffff; /* White border */
}

.message.ai {
  background-color: #262626; /* Dark gray */
  color: #ffffff; /* White text */
  margin-right: auto;
  border: 1px solid #ffffff; /* White border */
}

/* Input Area Styles */
.input-area {
  display: flex;
  gap: 10px;
  padding: 20px;
  background-color: #262626; /* Dark gray */
  border-top: 1px solid #ffffff; /* White border */
}

.chat-input {
  flex: 1;
  padding: 12px 16px; /* Slightly larger padding */
  background-color: #333333; /* Dark gray */
  border: 1px solid #ffffff; /* White border */
  border-radius: 25px; /* More rounded corners */
  font-size: 18px;
  color: #ffffff; /* White text */
  outline: none;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.chat-input::placeholder {
  color: #999999; /* Light gray placeholder */
}

.chat-input:focus {
  border-color: #ffffff; /* White border */
  box-shadow: 0 0 8px rgba(255, 255, 255, 0.5); /* White glow */
}

.send-button {
  padding: 12px 24px; /* Slightly larger padding */
  background-color: #333333; /* Dark gray */
  color: #ffffff; /* White text */
  border: 1px solid #ffffff; /* White border */
  border-radius: 25px; /* More rounded corners */
  font-size: 18px;
  font-weight: 600; /* Bold text */
  cursor: pointer;
  transition: background-color 0.3s ease, box-shadow 0.3s ease, transform 0.2s ease;
}

.send-button:hover {
  background-color: #444444; /* Slightly lighter gray */
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5); /* White glow */
  transform: scale(1.05); /* Slightly enlarge on hover */
}

.send-button:active {
  transform: scale(0.95); /* Slightly shrink on click */
}